import stampit from 'stampit';
import { visit } from './traversal/visitor';
var SymbolicExpressionsVisitor = stampit({
  props: {
    nestingLevel: 0,
    result: ''
  },
  methods: {
    enter: function (element) {
      var elementName = element.element;
      var capitalizedElementName = elementName.charAt(0).toUpperCase() + elementName.slice(1);
      var indent = '  '.repeat(this.nestingLevel);
      this.result += this.nestingLevel > 0 ? '\n' : '';
      this.result += "".concat(indent, "(").concat(capitalizedElementName, "Element");
      this.nestingLevel += 1;
    },
    leave: function () {
      this.nestingLevel -= 1;
      this.result += ')';
    }
  }
}); // transforms ApiDOM into S-expressions (Symbolic Expressions)

var sexprs = function (element) {
  var visitor = SymbolicExpressionsVisitor();
  visit(element, visitor);
  return visitor.result;
};

export default sexprs;